import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileImportTableListResponse } from "../../Model/Response/FileImportTableListResponse";
import { FileImportTableResponse } from "../../Model/Response/FileImportTableResponse";
import { FileImportTableViewModel } from '../../Model/ViewModel/FileImportTableViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";
import { NumberResponse } from "../../Model/Response/NumberResponse";

/*
  Generated class for the FileImportTableServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileImportTableServicesProvider {
  private FileImportTables: FileImportTableListResponse;
  private FileImportTable: FileImportTableResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileImportTable";
    console.log("Hello FileImportTableServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
     sub_url = "FileImportTable?PageSize={1}&PageNumber={2}&keyword={3}&exec_file_seq={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_file_seq,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileImportTables = new FileImportTableListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileImportTables;
    });
  }



  public PostPutAsync(FileImportTableViewModel:FileImportTableViewModel,mode:string) {
    var sub_url: string;

    if(mode==="POST")
      sub_url = "FileImportTable";
    else
    sub_url = "FileImportTable?exec_file_seq={1}&schemaname={2}&tablename={3}&db_conn_id={4}&parameter_column_group={5}";
    sub_url = String.Format(sub_url, this.ctl,FileImportTableViewModel.exec_file_seq,FileImportTableViewModel.schemaname, FileImportTableViewModel.tablename,FileImportTableViewModel.db_conn_id,FileImportTableViewModel.parameter_column_group);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileImportTableViewModel,mode).map(
      item => {
        this.FileImportTable = new FileImportTableResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileImportTable;
      }
    );
  }

  public DeleteAsync(exec_file_seq:number,schemaname:string, tablename:string,db_conn_id:string,parameter_column_group:string) {
    var sub_url: string;
    sub_url = "FileImportTable?exec_file_seq={1}&schemaname={2}&tablename={3}&db_conn_id={4}&parameter_column_group={5}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,schemaname, tablename,db_conn_id,parameter_column_group);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }

  public GetMaxExecSeqAsync(exec_file_seq:number) {
    var sub_url: string;
    sub_url = "FileImportTable/get_max_exec_seq?exec_file_seq={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(exec_file_seq:number,schemaname:string, tablename:string,db_conn_id:string,parameter_column_group:string,method:string) {
    var sub_url: string;
    sub_url = "FileImportTable/get_{6}_sql?exec_file_seq={1}&schemaname={2}&tablename={3}&db_conn_id={4}&parameter_column_group={5}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,schemaname, tablename,db_conn_id,parameter_column_group,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



