import { SingleModelResponse } from "./SingleModelResponse";
import { FileImportTableViewModel } from "../ViewModel/FileImportTableViewModel";

export class FileImportTableResponse extends SingleModelResponse<FileImportTableViewModel> {
  Model: FileImportTableViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileImportTableViewModel();
      this.Model.exec_file_seq=obj.Model.exec_file_seq;
      this.Model.schemaname=obj.Model.schemaname;
      this.Model.tablename=obj.Model.tablename;
      this.Model.db_conn_id=obj.Model.db_conn_id;
      this.Model.db_conn_string=obj.Model.db_conn_string;
      this.Model.parameter_column_group=obj.Model.parameter_column_group;
      this.Model.sheetname=obj && obj.Model.sheetname || null;
      this.Model.import_mode_key=obj.Model.import_mode_key;
      this.Model.file_format_key=obj.Model.file_format_key;
      this.Model.is_active=obj.Model.is_active;
      this.Model.field_terminator=obj && obj.Model.field_terminator || null;
      this.Model.row_terminator=obj.Model.row_terminator;
      this.Model.skip_row_num=obj.Model.skip_row_num;
      this.Model.skip_dup_key=obj.Model.skip_dup_key;
      this.Model.exec_seq=obj.Model.exec_seq;
      this.Model.load_complete_package=obj && obj.Model.load_complete_package || null;
      this.Model.sp_name=obj && obj.Model.sp_name || null;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;
      this.Model.import_mode = obj.Model.import_mode;
      this.Model.file_format = obj.Model.file_format;
      this.Model.is_active_desc=obj.Model.is_active_desc;
      this.Model.tablefullname=obj.Model.schemaname+"."+obj.Model.tablename;
      this.Model.is_before_sql=obj.Model.is_before_sql;
      this.Model.before_cnt=obj.Model.before_cnt;
      this.Model.after_cnt=obj.Model.after_cnt;
      if(this.Model.is_before_sql===true)
      {
        this.Model.before_sql_color="danger";
      }
      else
      this.Model.before_sql_color="light";


      if(this.Model.before_cnt>0)
      {
        this.Model.before_cnt_color="danger";
      }
      else
      this.Model.before_cnt_color="light";
      if(this.Model.after_cnt>0)
      {
        this.Model.after_cnt_color="danger";
      }
      else
      this.Model.after_cnt_color="light";

      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }


    }
  }
}



