export class FileImportTableViewModel {
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  結構描述
  /// <summary>
  public schemaname: string;
  /// <summary>
  ///  資料表
  /// <summary>
  public tablename: string;
  /// <summary>
  ///  db連線編號
  /// <summary>
  public db_conn_id: string;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  工作表名稱
  /// <summary>
  public sheetname: string;
  /// <summary>
  ///  匯入模式主鍵
  /// <summary>
  public import_mode_key: string;
  /// <summary>
  ///  檔案內文格式主鍵
  /// <summary>
  public file_format_key: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  欄位分隔符號
  /// <summary>
  public field_terminator: string;
  /// <summary>
  ///  列結尾符號
  /// <summary>
  public row_terminator: string;
  /// <summary>
  ///  略過資料列數
  /// <summary>
  public skip_row_num: number;
  /// <summary>
  ///  略過重複值
  /// <summary>
  public skip_dup_key: boolean;
  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  匯入後封裝名稱
  /// <summary>
  public load_complete_package: string;
  /// <summary>
  ///  預存程序名稱
  /// <summary>
  public sp_name: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  /// <summary>
  ///  匯入模式
  /// <summary>
  public import_mode: string;
  /// <summary>
  ///  檔案內文格式
  /// <summary>
  public file_format: string;

  /// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;
  /// <summary>
  ///  資料表全名
  /// <summary>
  public tablefullname: string;
  /// <summary>
  ///  是否有設定匯入前SQL語法?
  /// </summary>
  public is_before_sql: boolean;
  /// <summary>
  ///  before_Sql顏色
  /// </summary>
  public before_sql_color: string;
  /// <summary>
  ///  db連線字串
  /// <summary>
  public db_conn_string: string;

  /// <summary>
  ///  匯入前sql筆數
  /// <summary>
  public before_cnt: number;
 /// <summary>
  ///  匯入前sql顏色
  /// <summary>
  public before_cnt_color: string;
  /// <summary>
  ///  匯入後sql筆數
  /// <summary>
  public after_cnt: number;
  /// <summary>
  ///  匯入後sql顏色
  /// <summary>
  public after_cnt_color: string;
}



